body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  background: linear-gradient(to right, #8080ff, #ff1a1a);
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.button {
  cursor: pointer;
}

article {
  background: linear-gradient(-45deg, #8080ff, #ff1a1a);
  opacity: 0.7;
  border-radius: 6px !important;
}

.description {
  font-weight: bolder;
}
