.form {
  background: radial-gradient(
        circle at 0% 50%,
        rgba(96, 16, 48, 0) 9px,
        #613 10px,
        rgba(96, 16, 48, 0) 11px
      )
      0px 10px,
    radial-gradient(
      at 100% 100%,
      rgba(96, 16, 48, 0) 9px,
      #613 10px,
      rgba(96, 16, 48, 0) 11px
    ),
    #8a3;
  background-size: 20px 20px;
  box-shadow: 3px 4px rgba(0, 0, 0, 0.5);
  opacity: 0.8;
}

@media only screen and (max-width: 447px) {
  .but {
    font-size: 14px !important;
  }

  .description {
    font-size: 18px !important;
  }
}
