body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  background: -webkit-linear-gradient(left, #8080ff, #ff1a1a);
  background: linear-gradient(to right, #8080ff, #ff1a1a);
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.button {
  cursor: pointer;
}

article {
  background: -webkit-linear-gradient(135deg, #8080ff, #ff1a1a);
  background: linear-gradient(-45deg, #8080ff, #ff1a1a);
  opacity: 0.7;
  border-radius: 6px !important;
}

.description {
  font-weight: bolder;
}

.title {
  font-family: 'Fascinate Inline', cursive;
  position: absolute;
  left: 0;
  top: 0;
  font-size: 26px !important;
  display: inline-block;
  background-image: -webkit-linear-gradient(left, #b30000, #00004d);
  background-image: linear-gradient(to right, #b30000, #00004d);
  -webkit-background-clip: text;
  color: transparent;
}

.sign {
  font-family: 'Spicy Rice', cursive;
  color: #000033 !important;
}

.Tilt {
  background: -webkit-linear-gradient(135deg, #8080ff, #ff1a1a);
  background: linear-gradient(-45deg, #8080ff, #ff1a1a);
  opacity: 0.8;
  margin-left: 16px;
}

@media only screen and (max-width: 447px) {
  .Tilt {
    width: 100px !important;
    height: 100px !important;
  }
  .img {
    width: 50px !important;
  }
}

.form {
  background: -webkit-radial-gradient(
        0% 50%,
        circle,
        rgba(96, 16, 48, 0) 9px,
        #613 10px,
        rgba(96, 16, 48, 0) 11px
      )
      0px 10px,
    -webkit-radial-gradient(
      at 100% 100%,
      rgba(96, 16, 48, 0) 9px,
      #613 10px,
      rgba(96, 16, 48, 0) 11px
    ),
    #8a3;
  background: radial-gradient(
        circle at 0% 50%,
        rgba(96, 16, 48, 0) 9px,
        #613 10px,
        rgba(96, 16, 48, 0) 11px
      )
      0px 10px,
    radial-gradient(
      at 100% 100%,
      rgba(96, 16, 48, 0) 9px,
      #613 10px,
      rgba(96, 16, 48, 0) 11px
    ),
    #8a3;
  background-size: 20px 20px;
  box-shadow: 3px 4px rgba(0, 0, 0, 0.5);
  opacity: 0.8;
}

@media only screen and (max-width: 447px) {
  .but {
    font-size: 14px !important;
  }

  .description {
    font-size: 18px !important;
  }
}

.bounding-box {
	position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
